.c-mainNavArea {
  padding-top: 65px;
  width: 220px;
  height: 100%;
  background: #f3f4f6;
  border-right: 1px solid #d6d9e0;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  z-index: 500;

  @include media_mainNav {
    left: -220px;
    width: 0;
    height: 0;
    opacity: 0;
    transition: left 0.2s , opacity 0.1s 0.2s, width 0.1s 0.3s, height 0.3s;
  }
  ul {
    padding-left: 0;
  }
  &__nav {
    margin-bottom: 0;
    padding-left: 0;
  }
  &__navItem {

    ul {
      background: #fff;
      color: $navy70;
      font-size: 14px;
    }
    li {
      a {
        display: block;
        padding: 10px 40px;
        text-align: left;
        color: inherit;
        font-size: inherit;
        cursor: pointer;
        &:hover {
          text-decoration: none;
          background: $black05;
        }
      }
      a.is-active {
        background: $navy70;
        color: #fff;
      }
    }
  }
  &__navItemTitle {
    transform: translateX(0);
    display: block;
    border-bottom: 1px solid $dllNavy20;
    padding: 10px 15px;
    font-size: 16px;
    color: $navy80;
    cursor: pointer;
    position: relative;
    &::after {
      display: inline-block;
      width: 16px;
      height: 16px;
      content: "\f106";
      text-align: right;
      font-family: FontAwesome;
      font-size: 20px;
      color: #54687a;
      position: absolute;
      top: 7px;
      right: 15px;
    }
    &:hover {
      background: $black05;
      text-decoration: none;
    }
    &.collapsed::after {
      content: "\f107";
    }
    i {
      margin-right: .5em;
      color: $navy70;
      font-size: inherit;
    }
    span {
      color: $navy80;
    }
  }

  &__navItem:first-of-type > .c-mainNavArea__navItemTitle::after {
    display: none;
  }
  &__navItemSubTitle {
    display: block;
    border-bottom: 1px solid $dllNavy20;
    padding: 10px 35px;
    font-size: 16px;
    color: $navy80;
    cursor: pointer;
    position: relative;
    &:after {
      display: inline-block;
      width: 16px;
      height: 16px;
      content: "\f106";
      text-align: right;
      font-family: FontAwesome;
      font-size: 20px;
      color: #54687a;
      position: absolute;
      top: 7px;
      right: 15px;
    }
    &.collapsed::after {
      content: "\f107";
    }
    &:hover {
      text-decoration: none;
    }
    span {
      color: $navy80;
    }
  }
}
.c-mainNavArea.is-active{
  width: 220px;
  height: 100%;
  left: 0;
  opacity: 1;
  transition: left 0.2s 0.2s, opacity 0.1s 0.1s, width 0.1s, height 0.1s;
}


