1280px`is-active`<br>
1280px

Markup:
<!-- img-->
<div class="c-curtain"></div>
<img src="data:image/jpeg;base64,">

Styleguide 3.1
*/

.c-curtain {
  width: 0;
  height: 0;
  opacity:0;
}

.c-curtain.is-active {
  width: 0;
  height: 0;
  opacity:0;
  transition: opacity 0.3s 0.1s, width 0.1s, height 0.1s;
  @include media_mainNav {
    display: block;
    width: 100%;
    height: 100vh;
    opacity: 1;
    background: rgba(0,0,0,.6);
    z-index: 300;
    position: fixed;
  }
}