/*


`.c-contentsArea__cols`

Markup:
+b.c-contentsArea
  +e.cols
    +e.primaryCol
    +e.secondaryCol
// `img` 
img(style="width:100%" src="data:image/jpeg;base64,")

Styleguide 4.1
*/

.c-contentsArea {
  margin-left: 220px;
  background: $paleBlue;
  @include media_mainNav {
    margin-left: 0;
  }
  &__cols {
    display: table;
    padding: 0 15px 60px;
    width: 100%;
  }
  &__primaryCol {
    display: table-cell;
    vertical-align: top;
  }
  &__secondaryCol {
    display: table-cell;
    width: 350px;
    vertical-align: top;
  }
}


