.c-subContents {
  background: $paleBlue100;
  margin-bottom: 10px;
  padding: 15px;
  box-sizing: border-box;
  position: relative;
  &::before {
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent $paleBlue100 transparent;
    position: absolute;
    top: -9px;
    left: 10px;
  }
  &__action {
    text-align: center;
  }
}
